.navbar{
  position: fixed;
  background-color: #232b3b;
  color: green;
  width: 99vw;
  top: 0;
  left: 0;
  padding: 10px;
  z-index: 2;
}

.nav-title{
  float: left;
  background-color: inherit;
}

.nav-links{
  float: right;
  background-color: inherit;
  display: flex;
  gap: 13px;
  position: relative;
  margin-right: 1vw;
}

.nav-links a{
  background-color: inherit;
}

@media (orientation: portrait){
  .nav-links{
    margin-right: 3vw;
  }
}
