@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono:display=swap');

*{
  font-family: 'JetBrains Mono';
  background-color: #343F56;
  color: #F5E6CA;
}

body{
background-color: #343F56;

}

.App{
  padding: 10vh 10vw 5vh;
  display: grid;
  grid-template:
  'pen done'
  'pen done'
  'add add'
  /1fr 1fr;
}

.pending{
  grid-area: pen;
  min-height: 50vh;
  max-width: 40vw;
}

.done{
  grid-area: done;
  min-height: 50vh;
  max-width: 40vw;
}

.add{
  grid-area: add;

}

.addform{
  display: flex;
  flex-direction: column;
  max-width: 500px;
  font-size: 20px;
  margin-left: 10px;
}

.tasks{
  font-size: 20px;
  max-width: 30vw;

}

.tasks li{
    padding: 15px 0 20px;
    position: relative;
}

.li-text{
  max-width: 82%;
}


.tasks-btn{
  border: 0px;
  color: #6c6f75;
  font-size: 35px;
}
.tasks-btn-active{
  border: 0px;
  color: #FB9300;
  font-size: 35px;
}

.done-btn{
  border: 0px;
  color: #6c6f75;
  font-size: 35px;
}
.done-btn-active{
  border: 0px;
  color: #F54748;
  font-size: 35px;
}

.add-btn{
  border: 0px;
  font-size: 35px;
  color: #6c6f75;

}
.add-btn-active{
  border: 0px;
  font-size: 35px;
  color: #09ce38;

}

.btns{
  float: right;
  position: absolute;
  top: 21%;
  right: 0;

}


.dlt-btn{
  background-color: #F54748;
  border: 2px solid #F54748;
  color: #F54748;
  border-radius: 4px;

  height: 20px;
  width: 20px;
}

.mv-btn{
  background-color: #09ce38;

  border: 2px solid #09ce38;
  color: #09ce38;
  border-radius: 4px;

  height: 20px;
  width: 20px;

  margin-right: 5px;
  margin-left: 5px;
}

.addform input{
    background-color: #4f6082;
    border: 0px;
    border-radius: 5px;
    font-size: 20px;
    padding: 5px;
    outline: none;
    margin-top: 1vh;
}

#save-btn{
  max-width: 200px;
}

@media (orientation: portrait) {
  .App{
    padding: 7vh 7vw 2vh;
    display: flex;
    flex-direction: column;
    gap: 3vh;
  }

  .pending{
    min-height: 0.3vh;
    max-width: 80vw;
  }

  .done{
    min-height: 0.3vh;
    max-width: 80vw;
  }

  .tasks-btn{
    font-size: 3rem;
  }
  .tasks-btn-active{
    font-size: 3rem;
  }

  .done-btn{
    font-size: 3rem;
  }
  .done-btn-active{
    font-size: 3rem;
  }

  .tasks{
    font-size: 1.5rem;
    max-width: 80vw;
    line-height: 6vh;
  }

  .dlt-btn{
    height: 1.7rem;
    width: 1.7rem;
  }

  .mv-btn{
    height: 1.7rem;
    width: 1.7rem;
    margin-right: 4vw;
  }

  .add-btn{
    font-size: 3rem;
  }
  .add-btn-active{
    font-size: 3rem;
  }

  .addform{
    font-size: 1.5rem;
    margin-left: 10px;
  }

  .addform input{
      background-color: #4f6082;
      border: 0px;
      border-radius: 5px;
      font-size: 2rem;
      padding: 5px;
      outline: none;
  }

  .li-text{
    max-width: 80%;
  }
}
